.container {
    padding-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: 
    "dis dis dis dis"
    "AC C div times"
    "seven eight nine minus"
    "four five six plus"
    "one two three equal"
    "zero zero dot equal";
}

.padButton{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 40px;
    background-color: gray;
    color: white;
    border: 1px solid black;
    cursor: pointer;
    font-size: 20px;
}

.padButton:hover{
    border: 1px solid white;
    color: #ccc;
}

.dark-gray{
    background-color: #333;
}

.tomato{
    background-color: tomato;
}

.blue{
    background-color: #5599ff;
}

.dis{
    grid-area: dis;
    background-color: black;
    text-align: right;
    font-size: 20px;
}
.dis input{
    width:90%;
    color: white;
    padding: 5px 20px;
    text-align: right;
    background-color: black;
}

@media(max-width:460px){
    .dis input{
        width:85%;
        border-left: none;
    }
}

input::placeholder{
    color:white;
}

.total{
    padding: 5px 20px;
    color: yellow
}

.zero{
    grid-area: zero;
}

.equal {
    grid-area: equal;
}

h5 {
    padding-top: 25px;
    text-align: center;
}